import React from 'react';
import Swiper from 'swiper';
import Userheader from '../children/userheader.js';
import CurrentLearn from '../children/currentLearn';
import Learn from '../children/learn.js';
import './style.scss';

export default class defaultmine extends React.Component{
    constructor(){
        super();
        this.state = {
            touchRatio:0.5,
        }
    }

    render(){
        return (<>
        <div className="userinfo">
            <div className="swiper-container" style={{overflow: "inherit" }}>
                <div className="swiper-wrapper">
                <div className="swiper-slide">
                    <Userheader />
                    <CurrentLearn />
                    <Learn />
                </div>
                </div>
            </div>
            </div>
        </>)
    }

    componentDidMount(){
        const _that = this;
        _that.swiper = new Swiper('.swiper-container', {
            slidesPerView: 'auto',
            direction : 'vertical',
            spaceBetween: 30,
            touchRatio : _that.state.touchRatio,
            on: {
                
            },
        });
    }
}
