import React, { Component } from 'react'
import Defaultmine from './component/defaultmine.js';
import Setting from './children/setting.js';
import './style.scss'


const Haed = (props)=>{
  return (
  <div className="head">
      <span className="iconfont icon-shezhi" onClick={()=>props._that.setting()}></span>
      <h3 className="unloack">已成功解锁 6 个知识点</h3>
      <span className="iconfont icon-shouji1"></span>
   </div>
)}

let curruser = localStorage.getItem("curruser");
export default class mine extends Component {

  constructor(props){
    super(props);
    // 判断当前是否登陆
    (!curruser)&&(props.history.push("/login"));
    
    this.state = {
      isShow:true,
    }
    
  }

  setting(){
      this.setState({
        isShow:false,
      })
  }
  render() {
    let setComponent = null;
    if(this.state.isShow){
      setComponent = <>
          < Haed _that={this}/>
          < Defaultmine />
      </>
    }else{
      setComponent = <>
          < Setting _props={this.props}/>
      </>
    }

    return (
      <div className="page" id="mine">
           {setComponent}
      </div>
    )
  }

}
