import React,{Component} from 'react';

export default class Setting extends Component{
    constructor(props){
        super(props);
    }

    logOut(_this){
        _this.history.go("/home");
        localStorage.setItem("curruser","");
    }

    render(){
        const _this = this.props._props;
        return( 
          <div id="setting" style={{background:"#fff",height:"100%"}}>
            <h3 className="border-bottom">
                <span className="iconfont icon-fanhui" 
                style={{ position:'absolute',left:'5%',fontSize:"20px" }} 
                onClick={()=>window.location.pathname="/mine"}></span>设置
            </h3>

            <ul className="set">
                <li>
                    <span>账号绑定</span>
                      <span className="iconfont icon-qianjin" ></span>
                </li>
                <li>
                    <span>个人资料</span>
                      <span className="iconfont icon-qianjin" ></span>
                </li>
                <li>
                    <span>密码修改</span>
                   <span className="iconfont icon-qianjin" ></span>
                </li>
                <li>
                    <span>清除缓存</span>
                    <span className="iconfont icon-qianjin" ></span>
                </li>
                <li>
                    <span>只允许wifi下载</span>
                    <div className="mui-switch mui-active" style={{lineHeight:"20px"}}>
						<div className="mui-switch-handle"></div>
					</div>
                </li>
            </ul>
             
             <p className="logout"
             onClick={this.logOut.bind(this,_this)}>退出账号</p>
        </div>
        )
    }

    componentDidMount(){
        window.mui.init({
            swipeBack:true //启用右滑关闭功能
        });
        window.mui('.mui-content .mui-switch').each(function() { //循环所有toggle
            this.parentNode.querySelector('span').innerText = '状态：' + (this.classList.contains('mui-active') ? 'true' : 'false');
            /**
             * toggle 事件监听
             */
            this.addEventListener('toggle', function(event) {
                //event.detail.isActive 可直接获取当前状态
                this.parentNode.querySelector('span').innerText = '状态：' + (event.detail.isActive ? 'true' : 'false');
            });
        });
    }
}